.Gallery {
    display:flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 50px 0 0;
    background-image: url(/michellestdarling/static/media/parchment.48de3e0c.png);
    background-position: center;
    /* height: 100vh; */
    font-family: "Mystery Quest";
    box-sizing: border-box;
}

.card {
    box-sizing: border-box;
    width: 400px;
    height: 520px;
    padding-bottom: 2rem;
    margin: 3rem 0;
    background: #2e1f22;
    color: white;
    box-shadow: 2px 5px 15px rgba(0, 0, 0, .8);
    transition: .1s;
}
.card:hover{
    transform: translateY(-.5rem);
    transition: .3s;
    box-shadow: 5px 20px 25px rgba(0, 0, 0, .8);
}

.card-img-top {
    border-bottom: 2px solid white;
    height: 300px;
}

.card-body {
    padding: 2rem;
}

.card img{
    height: 300px;
}
.Title {
    padding: 250px 50px;
    height: 90vh;
    background-image: url("https://i.pinimg.com/originals/a3/63/49/a36349ef844164f39cab94740b88bf74.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    width: 100%;
  }
  
  
  .Title h1 {
    font-family: "Mystery Quest", cursive;
    color:#2e1f22;
    font-size: 5rem;
    text-shadow: 2px 5px 12px orange;
    -webkit-animation: shadow 5s ease;
            animation: shadow 5s ease;
  }
  
  
  .Title-header-text h3{
    width: 500px;
    font-family: "Mystery Quest";
    font-size: 2rem;
    color: rgb(236, 131, 11);
    -webkit-animation: shadow 5s ease;
            animation: shadow 5s ease;
    text-shadow: 2px 5px 12px orange;
  }

  #circle h3:hover i{
      color: rgb(236, 131, 11);
      opacity: 1;
  }
   
  i{
      font-size: .8em !important;
      padding: 0 20px;
      opacity: 0;
  }

  .icon-container{
      width: 50%;
      text-align: center;
      display: flex;
      justify-content: center;
      margin: auto;
      grid-gap: 5rem;
      gap: 5rem;
  }

  .icon-container i{
      opacity: 1;
      font-size: 3em !important;
      color: white;
      margin-top: 2rem !important;
  }

  .icon-container i:hover{
      opacity: .7;
  }

  .icon-container i.fa-etsy{
      font-size: 2.4em !important;
      margin-top: 2.3rem !important;
  }

  a{
      text-decoration: none;
      color:rgb(15, 122, 122);
  }

 #circle a:hover{
      color: rgb(236, 131, 11);
      text-decoration: none;
  }



  @-webkit-keyframes shadow{
    0%{
        text-shadow: none;
    }
    50%{
      text-shadow: 2px 5px 12px orange;
    }
    100%{
        text-shadow: 2px 5px 12px orange;
      
    }
}



  @keyframes shadow{
    0%{
        text-shadow: none;
    }
    50%{
      text-shadow: 2px 5px 12px orange;
    }
    100%{
        text-shadow: 2px 5px 12px orange;
      
    }
}

  .Title-stars{
    position: absolute;
  }
  
  .Title .star-1{
      position: absolute;
    width: 300px;
    margin-top: -230px;
  }

  .star-2{
    position: absolute;
    width: 200px;
    margin-left: 90%;
    transform: rotate(10deg);
  }

  .star-3{
    float:left;
    width: 100px;
    margin-top: 10rem;
    transform: rotate(155deg);
  }

  .pinup {
      margin-top: 175px;
      width: 500px;
  }

.btn-primary {
    /* position: absolute; */
    font-family: "Ruthie";
    width: 55%;
    height: 50px;
    color: white;
    border: hidden;
    font-size: 2.2rem;
    font-weight: 500;
    background:rgb(236, 131, 11);
    margin-top: 20px;
    align-self: center;
}

.btn-primary:hover{
    background: white;
    color:rgb(236, 131, 11);
    border:2px solid rgb(236, 131, 11);
    outline: none;
}

.btn-primary:focus{
    background: #2e1f22;
    border-color: white;
}

#circle{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 3em;
    gap: 3em;
    text-align: center;
    height: 600px;
    width: 600px;
    background: rgb(248, 226, 161);
    border-radius: 50%;
    left: 60%;
    bottom: 15%;
    color: rgb(15, 122, 122);
    font-family: "Mystery Quest";
    font-weight: 900;
    -webkit-animation: circleShadow .5s ease-in-out;
            animation: circleShadow .5s ease-in-out;
    /* box-shadow: 2px 5px 12px rgba(255, 166, 0, 0.644); */
    box-shadow: 2px 5px 12px rgba(255, 0, 0, 0.514);
}

@-webkit-keyframes circleShadow{
    0%{ 
        box-shadow: none;
        display: flex;
        opacity: 0;
        transform:translateY(500px)
    }

    50% {
        opacity: 0;
    }

    80%{
        opacity: .5;
    }
    
    100%{
      transform: translateY(0);
      display: flex;
    }
}

@keyframes circleShadow{
    0%{ 
        box-shadow: none;
        display: flex;
        opacity: 0;
        transform:translateY(500px)
    }

    50% {
        opacity: 0;
    }

    80%{
        opacity: .5;
    }
    
    100%{
      transform: translateY(0);
      display: flex;
    }
}

